<template>
  <div>
    <el-button type="text" @click="dialogFormVisible = true"></el-button>

    <el-dialog title="" :visible.sync="dialogFormVisible">
      <div>
        
        <el-input v-model="newType" placeholder="" size="medium" class="elInput"></el-input>
        <el-input v-model="newBandwidth" placeholder="" class="elInput"></el-input>
        <br /> 
        <el-input v-model="newTime_delay" placeholder="" class="elInput"></el-input>
        <el-input v-model="newPacket_loss_rate" placeholder="" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addNew"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogForm2Visible">
      <div>
        
        <el-input v-model="newType" placeholder="" size="medium" class="elInput"></el-input>
        <el-input v-model="newBandwidth" placeholder="" class="elInput"></el-input>
        <br />
        <el-input v-model="newTime_delay" placeholder="" class="elInput"></el-input>
        <el-input v-model="newPacket_loss_rate" placeholder="" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm2Visible = false"> </el-button>
        <el-button type="primary" @click="updateNew"> </el-button>
      </div>
    </el-dialog>

    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="type" label="" width="180"></el-table-column>
      <el-table-column prop="bandwidth" label="" width="180"></el-table-column>
      <el-table-column prop="time_delay" label="" width="180"></el-table-column>
      <el-table-column prop="Packet_loss_rate" label="" width="180"></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button @click="del_row(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData: [
        {
          type: "",
          bandwidth: ' 25Mb/s',
          time_delay: ' 20ms',
          Packet_loss_rate: ' 3.4*10-5',
          id: 1
        }
      ],
      dialogFormVisible: false,
      dialogForm2Visible: false,
      newType: "",
      newBandwidth: "",
      newTime_delay: "",
      newPacket_loss_rate: "",
      formLabelWidth: "",
      operationId: ""
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/AllFrom4K/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    addNew() {
      axios({
        method: "get",
        url: "/api/insertInto4K/",
        params: {
          type: this.newType,
          bandwidth: this.newBandwidth,
          time_delay: this.newTime_delay,
          Packet_loss_rate: this.newPacket_loss_rate,
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data
          alert("");
          this.dialogFormVisible = false;
        }
      });
    },
    handleClick(e) {
      this.operationId = e.id;
      this.newType = e.type,
      this.newBandwidth = e.bandwidth,
      this.newTime_delay = e.time_delay,
      this.newPacket_loss_rate = e.Packet_loss_rate,
      this.dialogForm2Visible = true
    },
    updateNew() {
      axios({
        method: "get",
        url: "/api/updateFrom4K/",
        params: {
          id: this.operationId,
          type: this.newType,
          bandwidth: this.newBandwidth,
          time_delay: this.newTime_delay,
          Packet_loss_rate: this.newPacket_loss_rate,
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
          alert('')
          this.dialogForm2Visible = false;
        } else {
          alert('')
          this.dialogForm2Visible = false;
        }
      })
    },
    del_row(e) {
      if(confirm('')) {
        axios({
          method: "get",
          url: "/api/deleteFrom4K/",
          params: {
            id: e.id
          }
        }).then(res => {
          if(res.data.success) {
            this.tableData = res.data.data;
            alert('')
          }
        })
      }
    }
  }
};``
</script>

<style lang="stylus" scoped>
.elInput {
  width: 250px;
  margin-top: 10px;
}
</style>>




