<template>
  <div>
    <h1>OLT </h1>
    <el-table :data="tableData" style="width: 100%" height="550">
      <el-table-column label="">
        <el-table-column
          v-for="(columnData,index) in tableInitData1"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="80"
        ></el-table-column>
      </el-table-column>
      <el-table-column label="">
        <el-table-column
          v-for="(columnData,index) in tableInitData2"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="80"
        ></el-table-column>
      </el-table-column>
      <el-table-column label="">
        <el-table-column
          v-for="(columnData,index) in tableInitData3"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="80"
        ></el-table-column>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      tableInitData1: [
        ["Link_name", ""],
        ["is_trunk", "trunk"],
        ["Link_level", ""],
        ["Link_physical_or_trunk_bandwidth", "(Mbps)"]
      ],
      tableInitData2: [
        ["Network_element_name", ""],
        ["Network_element_IP", "IP"],
        ["port_number", ""],
        ["region", ""],
        ["Subnet", ""],
        ["Network_element_type", ""],
        ["Network_element_role", ""],
        ["Port_name", ""],
        ["Port_type", ""],
        ["Port_physical_bandwidth", "(Mbps)"]
      ],
      tableInitData3: [
        ["Network_element_name1", ""],
        ["Network_element_IP1", "IP"],
        ["port_number1", ""],
        ["region1", ""],
        ["Subnet1", ""],
        ["Network_element_type1", ""],
        ["Network_element_role", ""],
        ["Port_name1", ""],
        ["Port_type1", ""],
        ["Port_physical_bandwidth1", "(Mbps)"]
      ],
      tableData: [
        {
          Subnet: "",
          Network_element_name: "",
          Network_element_type: "OLT",
          Network_element_role1: "",
          Port_type1: "",
          Network_element_type1: "LSW",
          port_number: "0/0/2/0",
          Network_element_IP1: "10.23.192.14",
          Port_physical_bandwidth: "",
          is_trunk: "NO",
          port_number1: "0/0/1/0",
          Network_element_name1: "",
          Network_element_role: "",
          Port_name: "",
          Port_type: "",
          Port_name1: "",
          Link_name: "1001",
          Link_level: "OLT-LSW",
          Subnet1: "",
          region: "",
          Network_element_IP: "10.45.36.16",
          Link_physical_or_trunk_bandwidth: "1",
          region1: "",
          Port_physical_bandwidth1: ""
        }
      ]
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/Link_reletion/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
};
</script>

<style>
</style>



