    font-weight: 700;
    color:#333;
    background-color: #fff;
    /* border-right: solid 1px grey; */
    overflow-y: scroll;

}
.app-right {
    width: calc(100% - 230px);
    height: calc(100% - 40px);
    padding:20px;
    overflow-y: scroll;
    overflow: auto
}
</style>



