<template>
  <div>
    <el-select v-model="value" placeholder="">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <div class="block">
      <el-date-picker v-model="time" type="month" placeholder=""></el-date-picker>
      <el-button></el-button>
    </div>
    <el-table :data="tableData" style="width: 100%" height="500">
      <el-table-column prop="time" label="" width="180" fixed></el-table-column>
      <el-table-column prop="infiltration_ratio" label="" width="180"></el-table-column>
      <el-table-column prop="demand_rate_ave" label="" width="180"></el-table-column>
      <el-table-column prop="demand_simultaneously" label="" width="180"></el-table-column>
      <el-table-column prop="broadband_user_demand_rate_ave" label="" width="180"></el-table-column>
      <el-table-column prop="live_user_rate_ave" label="" width="180"></el-table-column>
      <el-table-column prop="live_simultaneously" label="" width="180"></el-table-column>
      <el-table-column prop="broadband_user_live_rate_ave" label="" width="180"></el-table-column>
      <el-table-column prop="online_user_simultaneously" label="" width="180"></el-table-column>
      <el-table-column prop="online_user_rate_ave" label="" width="180"></el-table-column>
      <el-table-column prop="broadband_user_online_rate_ave" label="" width="180"></el-table-column>
      <el-table-column prop="broadband_user_rate_ave" label="" width="180"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      options: [
        {
          value: "",
          label: ""
        }
      ],
      value: "",
      time: "",
      sendMonth: "2017/6",
      tableData: []
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/calculateParametersWithIPTVAndBRAS/",
      params: {
            time: this.sendMonth
      }
    }).then(res => {
      if (res.data.success) {
        let temtableData = []
        let len = res.data.data.time.length
        for (let i=0;i<len;i++) {
          let a = {}
          temtableData.push(a)
        }
        let allData = res.data.data;
        for (let key in allData) {
          allData[key].forEach((item,index) => {
            console.log(item, index)
            temtableData[index][key] = item
            console.log(temtableData[index][key])
          })
        }
        console.log(temtableData)
        this.tableData = temtableData
      }
    });
  }
};
</script>

<style>
</style>



