<template>
  <div>
    <el-button type="text" @click="dialogFormVisible = true"></el-button>

    <el-dialog title="" :visible.sync="dialogFormVisible">
      <div>
        
        <el-input v-model="newType" placeholder="" size="medium" class="elInput"></el-input>
        <el-input v-model="newResolving_power" placeholder="" class="elInput"></el-input>
        <br />
        <el-input v-model="newFrame_rate" placeholder="" class="elInput"></el-input>
        <el-input v-model="newColor_bits" placeholder="" class="elInput"></el-input>
        <br />
        <el-input v-model="newCompression_technique" placeholder="" class="elInput"></el-input>
        <br />
        <el-input v-model="newMinimum_rate" placeholder="" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addNew"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogForm2Visible">
      <div>
        
        <el-input v-model="newType" placeholder="" size="medium" class="elInput"></el-input>
        <el-input v-model="newResolving_power" placeholder="" class="elInput"></el-input>
        <br />
        <el-input v-model="newFrame_rate" placeholder="" class="elInput"></el-input>
        <el-input v-model="newColor_bits" placeholder="" class="elInput"></el-input>
        <br />
        <el-input v-model="newCompression_technique" placeholder="" class="elInput"></el-input>
        <br />
        <el-input v-model="newMinimum_rate" placeholder="" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm2Visible = false"> </el-button>
        <el-button type="primary" @click="updateNew"> </el-button>
      </div>
    </el-dialog>

    <el-table :data="tableData" style="width: 100%" height='550'>
      <el-table-column prop="type" label="" width="180" fixed></el-table-column>
      <el-table-column prop="Resolving_power" label="" width="180"></el-table-column>
      <el-table-column prop="Frame_rate" label="" width="180"></el-table-column>
      <el-table-column prop="Color_bits" label="" width="180"></el-table-column>
      <el-table-column prop="compression_technique" label="" width="180"></el-table-column>
      <el-table-column prop="Minimum_rate" label="" width="(MB)"></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button @click="del_row(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData: [
        {
          type: "\u6807\u6e05 SD",
          Frame_rate: "25/30",
          Color_bits: 8,
          compression_technique: "H.264",
          Minimum_rate: 2,
          Resolving_power: "640*480",
          id: 1
        }
      ],
      dialogFormVisible: false,
      dialogForm2Visible: false,
      newType: "",
      newFrame_rate: "",
      newColor_bits: "",
      newCompression_technique: "",
      newMinimum_rate: "",
      newResolving_power: "",
      operationId: '',
      formLabelWidth: ""
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/AllFromBusiness_parameters/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    addNew() {
      axios({
        method: "get",
        url: "/api/insertIntoBusiness_parameters/",
        params: {
          type: this.newType,
          Frame_rate: this.newFrame_rate,
          Color_bits: this.newColor_bits,
          compression_technique: this.newCompression_technique,
          Minimum_rate: this.newMinimum_rate,
          Resolving_power: this.newResolving_power
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data
          alert("");
          this.dialogFormVisible = false;
        }
      });
    },
    handleClick(e) {
      this.operationId = e.id
      this.newType = e.type;
      this.newFrame_rate = e.Frame_rate;
      this.newColor_bits = e.Color_bits;
      this.newCompression_technique = e.compression_technique;
      this.newMinimum_rate = e.Minimum_rate;
      this.newResolving_power = e.Resolving_power;
      this.dialogForm2Visible = true;
    },
    updateNew() {
      axios({
        method: "get",
        url: "/api/updateFromBusiness_parameters/",
        params: {
          id: this.operationId,
          type: this.newType,
          Frame_rate: this.newFrame_rate,
          Color_bits: this.newColor_bits,
          compression_technique: this.newCompression_technique,
          Minimum_rate: this.newMinimum_rate,
          Resolving_power: this.newResolving_power
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
          alert('')
          this.dialogForm2Visible = false;
        } else {
          alert('')
          this.dialogForm2Visible = false;
        }
      })
    },
    del_row(e) {
      if (confirm("")) {
        axios({
          method: "get",
          url: "/api/deleteFromBusiness_parameters/",
          params: {
            id: e.id
          }
        }).then(res => {
          if (res.data.success) {
            this.tableData = res.data.data;
            alert("");
          } else {
            alert("");
          }
        });
      }
    }
  }
};
``;
</script>

<style lang="stylus" scoped>
.elInput {
  width: 250px;
  margin-top: 10px;
}
</style>>




