<template>
  <div>
    <el-button type="text" @click="dialogFormVisible = true"></el-button>

    <el-dialog title="" :visible.sync="dialogFormVisible">
      <div>
        
        <el-input v-model="newType" placeholder="" size="medium" class="elInput"></el-input><br />
        
        <el-input v-model="newChannel_num" placeholder="" class="elInput"></el-input><br />
        Mbps
        <el-input v-model="newOne_channel_bandwidth_request" placeholder="Mbps" class="elInput"></el-input><br />
        Mbps
        <el-input v-model="newTotal_channel_bandwidth_request" placeholder="Mbps" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addNew"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogForm2Visible">
      <div>
        
        <el-input v-model="newType" placeholder="" size="medium" class="elInput"></el-input><br />
        
        <el-input v-model="newChannel_num" placeholder="" class="elInput"></el-input><br />
        Mbps
        <el-input v-model="newOne_channel_bandwidth_request" placeholder="Mbps" class="elInput"></el-input><br />
        Mbps
        <el-input v-model="newTotal_channel_bandwidth_request" placeholder="Mbps" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm2Visible = false"> </el-button>
        <el-button type="primary" @click="updateNew"> </el-button>
      </div>
    </el-dialog>

    <el-table :data="tableData" style="width: 100%" height='500'>
      <el-table-column prop="type" label="" width="180"></el-table-column>
      <el-table-column prop="channel_num" label="" width="180"></el-table-column>
      <el-table-column prop="one_channel_bandwidth_request" label="Mbps" width="180"></el-table-column>
      <el-table-column prop="total_channel_bandwidth_request" label="Mbps" width="180"></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button @click="del_row(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <h1>{{this.total}}</h1>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData: [
        {
            "total_channel_bandwidth_request": 300.0,
            "type": "\u6807\u6e05SD",
            "id": 1,
            "one_channel_bandwidth_request": 2.0,
            "channel_num": 150
        },
      ],
      dialogFormVisible: false,
      dialogForm2Visible: false,
      newType: "",
      newTotal_channel_bandwidth_request: "",
      newOne_channel_bandwidth_request: "",
      newChannel_num: "",
      formLabelWidth: "",
      operationId: "",
      total: ''
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/AllFromLive_channel_bandwidth/"
    }).then(res => {
      if (res.data.success) {
        this.total = res.data.total
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    addNew() {
      axios({
        method: "get",
        url: "/api/insertIntoLive_channel_bandwidth/",
        params: {
          type: this.newType,
          channel_num: this.newChannel_num,
          one_channel_bandwidth_request: this.newOne_channel_bandwidth_request,
          total_channel_bandwidth_request: this.newTotal_channel_bandwidth_request,
        }
      }).then(res => {
        if (res.data.success) {
          this.total = res.data.total
          this.tableData = res.data.data
          alert("");
          this.dialogFormVisible = false;
        }
      });
    },
    handleClick(e) {
      this.operationId = e.id;
      this.newType = e.type,
      this.newChannel_num = e.channel_num,
      this.newOne_channel_bandwidth_request = e.one_channel_bandwidth_request,
      this.newTotal_channel_bandwidth_request = e.total_channel_bandwidth_request,
      this.dialogForm2Visible = true
    },
    updateNew() {
      axios({
        method: "get",
        url: "/api/updateFromLive_channel_bandwidth/",
        params: {
          id: this.operationId,
          type: this.newType,
          channel_num: this.newChannel_num,
          one_channel_bandwidth_request: this.newOne_channel_bandwidth_request,
          total_channel_bandwidth_request: this.newTotal_channel_bandwidth_request,
        }
      }).then(res => {
        if (res.data.success) {
          this.total = res.data.total
          this.tableData = res.data.data;
          alert('')
          this.dialogForm2Visible = false;
        } else {
          alert('')
          this.dialogForm2Visible = false;
        }
      })
    },
    del_row(e) {
      if(confirm('')) {
        axios({
          method: "get",
          url: "/api/deleteFromLive_channel_bandwidth/",
          params: {
            id: e.id
          }
        }).then(res => {
          if(res.data.success) {
            this.total = res.data.total
            this.tableData = res.data.data;
            alert('')
          }
        })
      }
    }
  }
};``
</script>

<style lang="stylus" scoped>
.elInput {
  width: 250px;
  margin-top: 10px;
}
</style>>




